:root {
  --mq-size-sm: 576px;
  --mq-size-md: 768px;
  --mq-size-lg: 992px;
  --mq-size-xl: 1200px;
  --theme-color-base: #121212;
  --text-color-base: #fff;
  --text-color-secondary: #FEBA08;
}

.title2 {
  font-family: Poppins, sans-serif;
  font-weight: 700;
  font-size: 22px;
  line-height: 133.69%;
  text-align: center;
  color: var(--text-color-secondary);
}

.btn {
  position: relative;
  text-align: center;
  padding: 5px 16px 5px 17px;
  border: 1px solid var(--text-color-secondary);
  background: transparent;
  font-size: 18px;
  line-height: 27px;
  color: var(--text-color-base);
  text-decoration: none;
  font-family: 'Roboto-medium', sans-serif;
}

.btn:hover {
  color: var(--text-color-secondary);
  border: 1px solid var(--text-color-base);
}

.menu__btn {
  width: 17px;
  height: 17px;
  position: relative;
  z-index: 6;
  cursor: pointer;
}

.menu__btn--line {
  display: block;
  position: absolute;
  width: 100%;
  height: 2px;
  margin-bottom: 2px;
  background-color: var(--text-color-base);
  transition-duration: 0.1s;
}

.menu__btn .first {
  top: 0%;
  transition-duration: 0.5s;
  transform-origin: 0% 0%;
}

.menu__btn .first-ex {
  top: 0%;
  transition-duration: 0.5s;
  transform-origin: 100% 0%;
}

.menu__btn .second {
  width: 13px;
  top: 25%;
  right: 0%;
}

.menu__btn .third {
  top: 50%;
}

.menu__btn .fourth {
  width: 13px;
  top: 75%;
  right: 0%;
}

.is-active {
  position: fixed;
  right: 3%;
}

.is-active .first {
  transform: skewY(45deg);
}

.is-active .first-ex {
  transform: skewY(-45deg);
}

.is-active .second {
  opacity: 0;
}

.is-active .third {
  opacity: 0;
}

.is-active .fourth {
  opacity: 0;
}

.btn-play {
  position: relative;
  width: 90px;
  height: 90px;
  background: transparent;
  border: none;
  z-index: 7;
}

.btn-play__icon {
  width: 100%;
  height: 100%;
}

.btn2 {
  box-sizing: border-box;
  padding: 15px 25px 15px 25px;
  border: none;
  background: var(--text-color-secondary);
  text-decoration: none;
  font-size: 21px;
  line-height: 21px;
  color: var(--theme-color-base);
  text-align: center;
  font-family: 'Roboto-medium', sans-serif;
}

.btn2:hover {
  padding: 14px 24px 14px 24px;
  color: var(--theme-color-base);
  background: var(--text-color-base);
  border: 1px solid var(--text-color-secondary);
}

