}

.form-big__note::placeholder {
  padding: 15px 0px 0px 5px;
  font-family: Poppins-regular, sans-serif;
  font-weight: 400;
  font-size: 13px;
  line-height: 15px;
  color: var(--theme-color-base);
}

.form-big__button {
  position: absolute;
  width: 113px;
  height: 51px;
  padding: 15px 35px 15px 35px;
  right: 0px;
  bottom: 0px;
  font-size: 18px;
  margin-bottom: 10px;
}

.form-big__button:hover {
  padding: 14px 34px 14px 34px;
}

.dim {
  width: 100%;
  height: 51px;
  margin: 10px 0px;
  padding: 0px;
  border-width: 0px;
}

.dim::placeholder {
  display: block !important;
  padding-left: 5px;
  font-family: Poppins-regular, sans-serif;
  font-weight: 400;
  font-size: 13px;
  line-height: 15px;
  color: var(--theme-color-base);
}

.field {
  position: relative;
  width: 100%;
}

[data-type='validator-error'] {
  font-size: 11px;
  position: absolute;
  z-index: 10;
  margin-top: 2px;
  color: #ffffff;
  background: #c83535;
  box-shadow: 0px 6px 2px -2px #d34d4d;
  padding: 4px 10px;
  border-radius: 1px;
  pointer-events: none;
}

[data-type='validator-error']::before {
  content: '';
  width: 0;
  height: 0;
  top: -5px;
  position: absolute;
  left: 6px;
  border-left: 5px solid rgba(0, 0, 0, 0);
  border-right: 5px solid rgba(0, 0, 0, 0);
  border-bottom: 5px solid #c83535;
}

.form-fb__wraper {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  jstify-content: end;
  position: relative;
}

.info__namelink {
  font-family: Roboto-regular;
  font-weight: 400;
  font-size: 18px;
  line-height: 21px;
  margin: 40px 0px 5px 0px;
}

.info__link {
  text-decoration: none;
  margin-bottom: 35px;
  font-family: Roboto-medium, sans-serif;
  font-weight: 500;
  font-size: 18px;
  line-height: 21px;
  color: var(--text-color-secondary);
}

.info__link:hover {
  text-decoration: underline;
}

.form-small {
  position: relative;
}

.form-small__namefield {
  margin: 35px 0px 20px 0px;
  font-family: Roboto-medium, sans-serif;
  font-weight: 500;
  font-size: 18px;
  line-height: 21px;
  cursor: pointer;
}

.form-small__button {
  position: absolute;
  width: 113px;
  height: 51px;
  padding: 15px 20px 15px 20px;
  right: 0px;
  bottom: 0px;
  font-size: 18px;
  margin-bottom: 10px;
}

.form-small__button:hover {
  padding: 14px 19px 14px 19px;
}

.soc-links {
  display: flex;
  align-items: center;
  jstify-content: flex-start;
}

.soc-links__link {
  width: 43px;
  height: 43px;
  padding: 7px;
  margin: 10px 10px 0px 0px;
  background: var(--text-color-secondary);
  transition-duration: 0.5s;
}

.soc-links__link:hover {
  background: var(--text-color-base);
  box-shadow: 4px 4px 1px rgba(255, 255, 255, 0.3);
}

.soc-links__link-icon {
  width: 100%;
  height: 100%;
}

.foot {
  background: var(--text-color-secondary);
}

.foot__wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.document {
  margin: 15px 0px;
  text-align: center;
}

.document__link {
  text-decoration: none;
  font-family: Roboto-medium, sans-serif;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  color: var(--theme-color-base);
}

.copyright {
  margin-bottom: 15px;
  font-family: Roboto-regular, sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: var(--text-color-base);
}

body {
  position: relative;
  background: var(--theme-color-base);
  color: var(--text-color-base);
}

.container {
  --container-width: 100%;
  width: var(--container-width);
  margin: 0 auto;
  padding: 0 15px;
}

@media (min-width: 576px) {
  .container {
    --container-width: var(--mq-size-sm);
  }
  .container .navigation {
    display: block;
  }
  .container .navigation__item {
    margin: 10px 5px 10px 15px;
  }
  .container .navigation__link {
    font-size: 9px;
  }
  .container .btn-create {
    display: block;
    padding: 4px 8px 4px 9px;
    font-size: 11px;
    line-height: 15px;
  }
  .container .menu__btn {
    display: none;
  }
  .intro-info {
    margin-top: 40px;
  }
  .intro-info__title {
    font-size: 40px;
  }
  .intro-info__text {
    font-size: 17px;
    margin-top: 15px;
  }
  .btn-play {
    margin: 30px auto;
  }
  .slider .slick-slide {
    padding: 0px 1vh;
  }
  .control {
    width: 30px;
    padding: 5px 8px 5px 8px;
    top: 0%;
  }
}

@media (min-width: 768px) {
  .container {
    --container-width: var(--mq-size-md);
  }
  .container .logo {
    display: block;
    width: 82px;
    height: 32px;
    margin: 10px 10px;
  }
  .container .navigation__item {
    margin: 10px 10px 10px 15px;
  }
  .container .navigation__link {
    font-size: 15px;
  }
  .container .btn-create {
    padding: 6px 14px 6px 13px;
    font-size: 15px;
  }
  .intro-info {
    margin-top: 50px;
  }
  .intro-info__title {
    font-size: 44px;
  }
  .intro-info__text {
    font-size: 20px;
    margin-top: 18px;
  }
  .intro-info .btn-play {
    margin: 40px auto;
  }
  .form-big {
    flex-wrap: wrap;
    height: 220px;
  }
  .form-big__button {
    right: 15px;
    bottom: 7px;
  }
  .field {
    width: 48%;
  }
  .form-fb__wraper {
    height: 240px;
    flex-wrap: wrap;
  }
  .info__namelink {
    margin-top: 38px;
  }
  .field--smal {
    width: 100%;
  }
  .foot__wrap {
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }
  .document {
    text-align: start;
  }
  .copyright {
    margin: 15px 0px;
  }
}

@media (min-width: 992px) {
  .container {
    --container-width: var(--mq-size-lg);
  }
  .container .logo {
    display: block;
    width: 92px;
    height: 42px;
    margin: 10px 10px;
  }
  .container .navigation__item {
    margin: 10px 20px 10px 20px;
  }
  .container .navigation__item__link {
    font-size: 18px;
  }
  .container .navigation__item .btn-create {
    padding: 8px 19px 8px 18px;
    font-size: 18px;
  }
  .intro-info {
    margin-top: 50px;
  }
  .intro-info__title {
    font-size: 48px;
  }
  .intro-info__text {
    font-size: 24px;
    margin-top: 18px;
  }
  .form-big__button {
    right: 19px;
  }
}

@media (min-width: 1200px) {
  .container {
    --container-width: var(--mq-size-xl);
  }
  .container .logo {
    display: block;
    width: 109px;
    height: 56px;
    margin: 10px 10px;
  }
  .intro-info {
    margin-top: 50px;
  }
  .intro-info__title {
    font-size: 52px;
  }
  .intro-info__text {
    font-size: 28px;
    margin-top: 18px;
  }
  .form-big__button {
    right: 23px;
  }
}
